___CSS_LOADER_EXPORT___.push([module.id, ".cardAction{text-align:left;font-size:.9rem!important}", "",{"version":3,"sources":["webpack://./resources/js/Pages/Giphy.vue"],"names":[],"mappings":"AAgIA,YACA,eAAA,CACA,yBACA","sourcesContent":["<template>\n  <v-container>\n    <v-row class=\"my-10\" align=\"center\" justify=\"center\">\n      <v-col cols=\"12\" shaped>\n        <c-text-field name=\"search\" clearable v-model=\"search\" label=\"search\" />\n        <v-card width=\"100%\" color=\"transparent\">\n          <v-card-text class=\"pa-8\">\n            <v-row>\n              <v-col\n                v-for=\"gif in giphyData\"\n                :key=\"gif.id\"\n                cols=\"12\"\n                sm=\"6\"\n                md=\"4\"\n              >\n                <v-card\n                  height=\"360\"\n                  shaped\n                  link\n                  class=\"d-flex flex-column pb-3\"\n                >\n                  <v-img\n                    @click.prevent=\"switchImage(gif)\"\n                    :src=\"gif.media.thumbnail_url\"\n                    :ref=\"gif.id\"\n                    width=\"100%\"\n                    height=\"200px\"\n                  />\n\n                  <v-card-actions\n                    class=\"d-flex flex-column\"\n                    style=\"height: 100px\"\n                  >\n                    <v-card-title class=\"cardAction\">{{\n                      gif.title\n                    }}</v-card-title>\n\n                    <a\n                      class=\"v-btn cardAction mt-1 v-btn--flat v-btn--text\"\n                      :href=\"gif.short_url\"\n                      target=\"_blank\"\n                      style=\"font-size: 12px !important\"\n                    >\n                      see this image on giphy\n                    </a>\n                  </v-card-actions>\n                </v-card>\n              </v-col>\n            </v-row>\n          </v-card-text>\n          <v-card-actions\n            class=\"primary white--text justify-center pa-5\"\n            @click=\"loadMore\"\n            style=\"cursor: pointer\"\n            v-if=\"!search\"\n          >\n            <v-btn text :loading=\"loading\" class=\"white--text\">\n              Load More\n            </v-btn>\n          </v-card-actions>\n        </v-card>\n      </v-col>\n    </v-row>\n  </v-container>\n</template>\n\n<script>\nimport AppLayout from \"@/Layouts/Layout\";\nimport { debounce } from \"lodash/function\";\nexport default {\n  layout: AppLayout,\n  props: [\"giphy\"],\n  metaInfo() {\n    return {\n      title: this.pageTitle,\n    };\n  },\n  watch: {\n    search: {\n      handler: debounce(function () {\n        this.searchData();\n      }, 1000),\n      deep: true,\n    },\n  },\n\n  data() {\n    return {\n      pageTitle: \"AcmeGif Giphy\",\n      giphyData: this.giphy.data,\n      next: this.giphy.next,\n      loading: false,\n      search: \"\",\n    };\n  },\n  methods: {\n    switchImage(gif) {\n      this.$refs[gif.id][0].src = gif.media.image_url;\n    },\n\n    async loadMore(next = null) {\n      this.loading = true;\n      let nextPage = next == null ? 0 : this.next;\n      await axios.get(\"/giphy?offset=\" + nextPage).then((res) => {\n        this.loading = false;\n\n        this.giphyData.push(...res.data.data);\n        this.next = res.data.next;\n      });\n    },\n    async searchData() {\n      if (this.search) {\n        await axios.get(\"/giphy/search?q=\" + this.search).then((res) => {\n          this.loading = false;\n\n          this.giphyData = res.data.data;\n          this.next = res.data.next;\n        });\n      } else {\n        this.giphyData = [];\n        this.loadMore(null);\n      }\n    },\n  },\n};\n</script>\n\n<style>\n.cardAction {\n  text-align: left;\n  font-size: 0.9rem !important;\n}\n</style>\n"],"sourceRoot":""}]);
